import uCharts from '../../components/u-charts.js';
var _self;
export default {
	data() {
		return {
			cWidth: '',
			cHeight: '',
			pixelRatio: 1,
			serverData: '',
		}
	},
	onLoad() {
		_self = this;
		this.cWidth = uni.upx2px(750);
		this.cHeight = uni.upx2px(500);
	},
	onReady() {
		this.getServerData();
	},
	methods: {
		getServerData() {
			uni.showLoading({
				title: "正在加载数据..."
			})
			uni.request({
				url: 'https://unidemo.dcloud.net.cn/hello-uniapp-ucharts-data.json',
				data: {},
				success: function(res) {
					_self.fillData(res.data);
				},
				fail: () => {
					_self.tips = "网络错误，小程序端请检查合法域名";
				},
				complete() {
					uni.hideLoading();
				}
			});
		},
		fillData(data) {
			this.serverData = data;
			let Ring = {
				series: []
			};
			Ring.series = data.Ring.series;
			//自定义文案示例，需设置format字段
			for (let i = 0; i < Ring.series.length; i++) {
				Ring.series[i].format = () => {
					return Ring.series[i].name + Ring.series[i].data
				};
			}
			this.showRing("canvasRing", Ring);
		},
		showRing(canvasId, chartData) {
			new uCharts({
				$this: _self,
				canvasId: canvasId,
				type: 'ring',
				fontSize: 11,
				legend: true,
				title: {
					name: '70%',
					color: '#7cb5ec',
					fontSize: 25 * _self.pixelRatio,
					offsetY: -20 * _self.pixelRatio, //新增参数，自定义调整Y轴文案距离
				},
				subtitle: {
					name: '收益率',
					color: '#666666',
					fontSize: 15 * _self.pixelRatio,
					offsetY: 30 * _self.pixelRatio, //新增参数，自定义调整Y轴文案距离
				},
				extra: {
					pie: {
						lableWidth: 15,
						ringWidth: 40 * _self.pixelRatio, //圆环的宽度
						offsetAngle: -45 //圆环的角度
					}
				},
				background: '#FFFFFF',
				pixelRatio: _self.pixelRatio,
				series: chartData.series,
				animation: false,
				width: _self.cWidth * _self.pixelRatio,
				height: _self.cHeight * _self.pixelRatio,
				disablePieStroke: true,
				dataLabel: true,
			});

		},
	
	}
}
